<template>
  <div class="Exchange">
    <h1>汇率转化器</h1>
    <div class="functionBlock">
      <div class="part1">
        <el-input type="number" v-model="input1" placeholder="请输入内容" style="width: 221px"></el-input>
        =
        <!-- <el-input v-model="input2" placeholder="请输入内容" style="width: 221px">{{ resultNum }}</el-input> -->
        <span>{{ resultNum }}</span>
      </div>
      <br />
      <div class="part2">
        <el-select v-model="value1" placeholder="请选择" @change="isChangeFir">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"> </el-option>
        </el-select>
        :
        <el-select v-model="value2" placeholder="请选择" @change="isChangeSec">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"> </el-option>
        </el-select>
        =
        <span>{{ exchangeRate }}</span>
      </div>
    </div>
    <a href="#" @click="help">点击查看使用规则</a>
    <el-button type="primary" round @click="getData" class="query">发送请求</el-button>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  data() {
    return {
      options: [
        {
          value: 'CNY',
          label: '人民币'
        },
        {
          value: 'USD',
          label: '美元'
        },
        {
          value: 'HKD',
          label: '港币'
        },
        {
          value: 'EUR',
          label: '欧元'
        },
        {
          value: 'JPY',
          label: '日元'
        },
        {
          value: 'GBP',
          label: '英镑'
        },
        {
          value: 'TWD',
          label: '台币'
        },
        {
          value: 'INR',
          label: '卢比'
        }
      ],
      value1: '',
      value2: '',
      exchangeRate: 0,
      input1: 0,
      input2: 0,
      resultNum: 0
    }
  },
  methods: {
    getData() {
      axios.get(`https://free.currconv.com/api/v7/convert?q=${this.value1}_${this.value2}&compact=ultra&apiKey=3cb9a9fad02f09d421c5`).then(res => {
        console.log(res.data)
        const str = JSON.stringify(res.data)
        const regex = /[-+]?[0-9]*\.?[0-9]+/

        const matches = str.match(regex)
        if (matches && matches.length > 0) {
          const number = parseFloat(matches[0])
          // console.log(number)
          this.exchangeRate = number
          this.resultNum = (this.input1 * this.exchangeRate).toFixed(2)
        } else {
          console.log('No number found in the string.')
        }
      })
      // console.log(this.value1)
    },
    isChangeFir() {
      if (this.value2 === '') {
        return
      } else {
        this.getData()
      }
    },
    isChangeSec() {
      if (this.value1 === '') {
        return
      } else {
        this.getData()
      }
    },
    help() {
      alert('上方输入要转换的金额,只支持输入数字,右边输出转换后的值.下方选择要转换的货币,右边是两种货币的汇率')
    }
  }
}
</script>

<style lang="less">
.Exchange {
  width: 600px;
  height: 400px;
  border: 3px solid green;
  border-radius: 15px;
  background-color: rgb(0, 191, 0);
  padding: 20px 40px;
  position: relative;

  .query {
    position: absolute;
    bottom: 10%;
    right: 10%;
  }

  a {
    position: absolute;
    bottom: 10%;
  }
}

.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
</style>
